
@import url('https://fonts.googleapis.com/css2?family=Combo&family=Great+Vibes&display=swap');
*{
  margin: 0;
  box-sizing: border-box;
  list-style: none;
  font-family: 'Combo', cursive;

}
/* navbar css */
.nav{
  margin: auto;
  width: 100%;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: rgb(255, 255, 255);
  z-index: 2;
  position: fixed;
}
.nav_brand{
  font-size: 30px; 
  font-weight: bold;
  z-index: 2;
  padding-right: 380px;
  font-family: 'Great Vibes', cursive;
}
.nav_brand:hover{
  font-size: 35px; 
  cursor: pointer;
  text-shadow: rgb(30, 0, 137) 1px 0 10px;
  transition: all .5s;
}

.nav_menu
{
  display: flex;
  align-items: center;
  justify-content: space-around;
  gap:2rem;
}

li{
  font-weight: bold;
}
li:hover{
  font-size: 18px;
  cursor: pointer;
  text-shadow: rgb(30, 0, 137) 1px 0 10px;
  transition: all .5s;
}
.nav_toggler 
{
  cursor: pointer;
  display: none;  
}
/* navbar css end */

